/* Задний затемнённый фон */
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* Затемнение */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

/* Основное окно модального окна */
.modal {
    background: white;
    width: 90%;
    max-width: 800px;
    padding: 40px;
    border-radius: 12px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Кнопка закрытия */
.closeButton {
    position: absolute;
    top: 7px;
    right: 9px;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 18px;
    transition: transform 0.2s ease;
}

.closeButton:hover {
    transform: scale(1.2);
}

/* Заголовок */
.title {
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
}

/* Контейнер информации о загранпаспорте */
.passportBox {
    width: 100%;
    background: #f9f9f9;
    padding: 15px;
    border-radius: 8px;
    margin-bottom: 20px;
    text-align: center;
}

/* Заголовок паспорта */
.passportHeader h3 {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
}

/* Описание паспорта */
.passportBox p {
    font-size: 14px;
    color: #333;
}

/* Контейнер формы */
.contactContainer {
    width: 100%;
}

/* Входные поля */
.inputWrapper {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
}

.inputWrapper label {
    text-align: start;
    font-size: 14px;
    margin-bottom: 5px;
    font-weight: 500;
}

.inputField {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 14px;
    transition: border 0.2s ease;
}

.inputField:focus {
    border: 1px solid #ffc107;
    outline: none;
}

/* Ошибки */
.errorText {
    color: red;
    font-size: 12px;
    margin-top: 5px;
}

/* Контейнер для кнопки и чекбокса */
.contactActions {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

/* Чекбокс */
.checkboxLabel {
    margin-bottom: 25px;
    font-size: 14px;
    display: flex;
    align-items: center;
    gap: 8px;
}

.checkboxLabel input {
    width: 16px;
    height: 16px;
    cursor: pointer;
}

/* Кнопка отправки */
.submitButton {
    background: #ffe500;
    color: black;
    font-size: 16px;
    font-weight: bold;
    padding: 12px 20px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.2s ease;
    width: 100%;
    text-align: center;
}

.submitButton:hover {
    background: #e0a800;
    transform: scale(1.05);
}

.submitButton:disabled {
    background: #ccc;
    cursor: not-allowed;
}

.popupOverlay {
    z-index: 9999;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
}

.popup {
    color: black;
    background: white;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.popup h3 {
    margin-bottom: 10px;
}

.popup p {
    margin-bottom: 20px;
}

.popupButton {
    background: #111;
    color: white;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
}

.popupButton:hover {
    background: #333;
}


/* 🔹 Адаптивность */
@media (max-width: 480px) {
    .modal {
        width: 95%;
        padding: 20px;
    }

    .title {
        font-size: 20px;
    }

    .submitButton {
        font-size: 14px;
    }
}
